import React from 'react'
import { Draggable, Droppable } from 'react-beautiful-dnd';
import { Card, Tooltip, Tag } from 'antd';
import { CalendarOutlined, CommentOutlined, PaperClipOutlined, FileDoneOutlined } from '@ant-design/icons';
import moment from 'moment';
import { Scrollbars } from 'react-custom-scrollbars';
import { ProgressBar } from 'react-bootstrap';

const InnerCardList = React.memo(function InnerCardList(props) {
  let { contents, progress } = props;

  return contents?.map((item, index) => {
    return (
      <Draggable key={index} draggableId={Math.random().toString()} index={index}>
        {(dragProvided, dragSnapshot) => (
          <div
            className='mb-3'
            key={index}
            ref={dragProvided.innerRef}
            {...dragProvided.draggableProps}
            {...dragProvided.dragHandleProps}
          >
            <Card
              hoverable
              className='board-card text-center'
              cover={null}
              onClick={() => console.log(item)}
            >
              <div>
                <h4 style={{ color: '#3e82f7' }} className="mb-2 text-center">{item.client_companyname}</h4>
                <h6 className="mb-2">Contact Person: {item.client_contactperson}</h6>
                {
                  progress &&
                  <ProgressBar
                    animated
                    now={(item.completeordercount / item.ordercount) * 100}
                    label={`${item.completeordercount}/${item.ordercount}`}
                  />
                }
                <div className="d-flex justify-content-between align-items-center mt-3">
                  <div className="ahm-card-center">
                    <Tag color='blue'>
                      <CalendarOutlined />
                      <span className="ml-1 font-weight-semibold">{moment(item.created_at).format('DD MMM YY')}</span>
                    </Tag>
                    <span className="text-right mr-2" style={{ fontSize: 14 }}>{item.createby}</span>
                  </div>
                </div>
                <div className="d-flex mt-3">
                  <SubIndicator counts={item.ordercount} icon={<FileDoneOutlined style={{ color: '#3e82f7' }} />} />
                  <SubIndicator counts={item.attchmentcount} icon={<PaperClipOutlined style={{ color: '#3e82f7' }} />} />
                </div>
              </div>
            </Card>
          </div>
        )}
      </Draggable>
    )
  })
});

function InnerList(props) {
  const { contents, dropProvided, cardData, listId, progress } = props;

  return (
    <div className="board-dropzone" ref={dropProvided.innerRef}>
      <InnerCardList cardData={cardData} contents={contents} listId={listId} progress={progress} />
      {dropProvided.placeholder}
    </div>
  );
}

function SubIndicator(props) {
  if (props.counts) {
    return (
      <p className="mb-0 mr-2">
        {props.icon}
        <span className="ml-1">{props.counts}</span>
      </p>
    )
  }
  return null
}

export default function BoardCard(props) {
  const {
    ignoreContainerClipping,
    internalScroll,
    scrollContainerStyle,
    isDropDisabled,
    isCombineEnabled,
    listId = 'LIST',
    listType,
    style,
    contents,
    useClone,
    cardData,
    progress
  } = props;
  return (
    <>
      <Droppable
        droppableId={listId}
        type={listType}
        ignoreContainerClipping={ignoreContainerClipping}
        isDropDisabled={isDropDisabled}
        isCombineEnabled={isCombineEnabled}
        renderClone={useClone}
      >
        {(
          dropProvided,
          dropSnapshot,
        ) => (
          <Scrollbars style={style} className="board-wrapper" autoHide {...dropProvided.droppableProps}>
            {internalScroll ? (
              <div className="board-scrollContainer" style={scrollContainerStyle}>
                <InnerList
                  contents={contents}
                  listId={listId}
                  cardData={cardData}
                  dropProvided={dropProvided}
                  progress={progress}
                />
              </div>
            ) : (
                <InnerList
                  contents={contents}
                  listId={listId}
                  cardData={cardData}
                  dropProvided={dropProvided}
                  progress={progress}
                />
              )}
          </Scrollbars>
        )}
      </Droppable>
    </>
  );
}